.section__header {
  margin-bottom: 20px;
  padding: 5px;
  color: #b8bd56;
  font-size: 1.6rem;
  font-family: 'museo100';
  text-align: center; }
  .section__header span {
    position: relative;
    font-family: inherit;
    background: -webkit-gradient(linear, left top, right top, color-stop(20%, #b8bd56), to(#0cc));
    background: -webkit-linear-gradient(left, #b8bd56 20%, #0cc);
    background: -o-linear-gradient(left, #b8bd56 20%, #0cc);
    background: linear-gradient(to right, #b8bd56 20%, #0cc);
    background-clip: text;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
    .section__header span:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 1px;
      background: -webkit-gradient(linear, left top, right top, from(#b8bd56), to(#0cc));
      background: -webkit-linear-gradient(left, #b8bd56, #0cc);
      background: -o-linear-gradient(left, #b8bd56, #0cc);
      background: linear-gradient(to right, #b8bd56, #0cc);
      opacity: 0.2; }
    .ie .section__header span {
      background: none; }



