.main-menu {
  overflow: hidden;
  padding-top: 15px;
  text-align: right;
  -webkit-transition: 1s height;
  -o-transition: 1s height;
  transition: 1s height; }

.main-menu_fixed,
.main-menu_mobile {
  position: fixed;
  top: 0;
  right: 8px;
  z-index: 10; }

.main-menu_mobile {
  height: 40px; }

.main-menu_mobile_opened {
  height: auto; }

.main-menu__item-holder {
  display: inline-block;
  text-align: center;
  padding-bottom: 2px; }
  .main-menu__item-holder:nth-child(3n+4):not(:nth-last-child(4)):not(:last-child) {
    margin-right: 30px; }
    @media screen and (max-width: 1024px) {
      .main-menu__item-holder:nth-child(3n+4):not(:nth-last-child(4)):not(:last-child) {
        margin-right: 15px; } }
  .main-menu_fixed .main-menu__item-holder,
  .main-menu_mobile .main-menu__item-holder {
    display: block; }
    .main-menu_fixed .main-menu__item-holder:nth-child(3n+4):not(:nth-last-child(4)):not(:last-child),
    .main-menu_mobile .main-menu__item-holder:nth-child(3n+4):not(:nth-last-child(4)):not(:last-child) {
      margin-right: 0;
      margin-bottom: 10px; }

.main-menu__item-holder_mobile-button {
  display: none;
  text-align: center; }
  .main-menu_mobile .main-menu__item-holder_mobile-button {
    display: block; }

.main-menu__item-holder_closed {
  display: none !important; }

.main-menu__item-link {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  height: 50px;
  width: 50px;
  text-align: center;
  cursor: pointer;
  opacity: 1;
  -webkit-transition: 0.5s opacity !important;
  -o-transition: 0.5s opacity !important;
  transition: 0.5s opacity !important;
  font-size: 0;
  background-image: url("../../images/menu-sprite.png");
  background-size: cover;
  background-repeat: no-repeat; }
  @media screen and (min-width: 480px) {
    .main-menu__item-link {
      margin-right: 10px;
      opacity: 0.4; } }
  .main-menu__item-link.active, .main-menu__item-link:hover {
    opacity: 1; }
  .main-menu__item-link:focus {
    -webkit-filter: drop-shadow(2px 2px 2px #222);
    filter: drop-shadow(2px 2px 2px #222); }
  .main-menu_fixed .main-menu__item-link,
  .main-menu_mobile .main-menu__item-link {
    width: 25px;
    height: 25px;
    margin-bottom: 3px; }
  @media screen and (max-width: 1024px) {
    .main-menu__item-link {
      height: 25px;
      width: 25px; } }

.main-menu__item-link_mobile-button {
  text-align: center;
  width: 25px;
  height: 25px; }
  .main-menu__item-link_mobile-button span {
    color: white;
    font-size: 1.3rem;
    -webkit-transition: 0.5s color;
    -o-transition: 0.5s color;
    transition: 0.5s color;
    padding-bottom: 10px; }
  .main-menu__item-link_mobile-button:hover span,
  .main-menu_fixed .main-menu__item-link_mobile-button span {
    color: #b8bd56; }

.service-link {
  display: none; }

.modal-overlay {
  display: flex;
  display: -ms-flexbox; }
  @media screen and (max-width: 500px) {
    .modal-overlay {
      -ms-flex-align: center !important;
      align-items: center !important; } }

.modal-overlay_balls {
  -ms-flex-align: center !important;
  align-items: center !important; }

.modal-modal {
  background: transparent !important;
  padding: 0 !important; }
  .modal-modal > img {
    max-width: 100%; }



