* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Helvetica;
  outline: none !important; }
  *::selection {
    color: blue; }

html {
  font-size: 16px;
  line-height: 20px;
  overflow-x: hidden; }

html:lang(ru) *:not(:lang(ru)) {
  display: none; }

html:lang(uk) *:not(:lang(uk)) {
  display: none; }

html:lang(en) *:not(:lang(en)) {
  display: none; }

main {
  padding: 0 5%; }

section {
  padding: 50px 15px 0 0;
  max-width: 1280px;
  margin: 0 auto; }
  @media screen and (min-width: 768px) {
    section {
      padding: 50px 25px 25px; } }

a {
  text-decoration: none; }

cite {
  font-style: normal; }

ul, ol {
  list-style-type: none; }

.loader {
  height: 4px;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: #ddd; }

.loader:before {
  display: block;
  position: absolute;
  content: "";
  left: -200px;
  width: 200px;
  height: 4px;
  background-color: #2980b9;
  animation: loading 2s linear infinite; }

@keyframes loading {
  from {
    left: -200px;
    width: 30%; }
  50% {
    width: 30%; }
  70% {
    width: 70%; }
  80% {
    left: 50%; }
  95% {
    left: 120%; }
  to {
    left: 100%; } }

@keyframes shine {
  to {
    background-position: 100% center; } }

@keyframes svg-shine {
  0% {
    fill: white; }
  30% {
    fill: #b8bd56; }
  80% {
    fill: #0cc; }
  100% {
    fill: blue; } }

.header_primary {
  position: relative;
  height: 300px;
  background: linear-gradient(to right, white 0%, #b8bd56 100%);
  overflow: hidden; }

.header__avatar {
  position: relative;
  left: -100px;
  max-width: none !important;
  opacity: .6; }
  @media screen and (min-width: 769px) {
    .header__avatar {
      left: -50px; } }

.header__name {
  position: absolute;
  top: 0;
  right: 40px;
  font-size: 2rem;
  line-height: 2rem;
  font-weight: normal;
  font-family: 'museo100';
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  text-shadow: 5px 10px 5px rgba(0, 0, 0, 0.3);
  color: white;
  text-align: right;
  transition: text-shadow 1s, letter-spacing 1s; }
  @media screen and (min-width: 480px) {
    .header__name {
      font-size: 2.5rem;
      line-height: 2.5rem;
      font-weight: normal;
      font-family: 'museo100';
      letter-spacing: 0.5rem; } }
  @media screen and (min-width: 768px) {
    .header__name {
      font-size: 3.5rem;
      line-height: 3.5rem;
      font-weight: normal;
      font-family: 'museo100';
      top: 10px;
      right: 20px; } }

.header__name_lastname {
  font-family: 'museo900'; }
  @media screen and (max-width: 768px) {
    .header__name_lastname {
      display: block; } }

.header__name_post {
  display: block;
  font-size: 1.2rem;
  line-height: 1.2rem;
  font-weight: normal;
  text-transform: none; }
  @media screen and (min-width: 480px) {
    .header__name_post {
      font-size: 1.5rem; } }
  @media screen and (min-width: 768px) {
    .header__name_post {
      font-size: 2rem; } }

.header__contacts {
  position: absolute;
  right: 40px;
  bottom: 10px;
  display: flex;
  display: -ms-flexbox;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  font-family: 'robotob';
  color: white; }
  @media screen and (min-width: 480px) {
    .header__contacts {
      width: 75%;
