.interests-list {
  position: relative;
  padding-top: 50px;
  height: 150px;
  margin-right: 20px; }
  .interests-list .slick-track {
    -webkit-transition: 1s !important;
    -o-transition: 1s !important;
    transition: 1s !important; }
  .interests-list .slick-arrow {
    margin-top: 15px; }
    .interests-list .slick-arrow:before {
      content: '';
      position: absolute;
      width: 20px;
      height: 20px;
      border: 4px solid skyblue;
      border-bottom: none;
      border-right: none;
      -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
      -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
      transform-origin: center center; }
    .interests-list .slick-arrow.slick-next {
      right: 0; }
      .interests-list .slick-arrow.slick-next:before {
        -webkit-transform: rotate(135deg);
        -ms-transform: rotate(135deg);
        transform: rotate(135deg); }
    @media screen and (max-width: 480px) {
      .interests-list .slick-arrow.slick-prev {
        left: 0; } }
  .interests-list .slick-slide {
    position: relative;
    padding-left: 10px;
    text-align: center; }

.interests-item__image {
  display: inline-block;
  max-width: 100px;
  max-height: 100px;
  margin: 0 auto; }

.interests-item__taboo {
  position: absolute;
  font-size: 102px;
  left: 12px;
  right: 0;
  margin: auto;
  z-index: 1;
  color: red;
  opacity: 0.3;
  pointer-events: none; }
  .interests-item__taboo.glyphicon {
    position: absolute; }

.interests-item__description {
  display: none; }



