.portfolio-item {
  position: relative;
  display: inline-block;
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
  vertical-align: top;
  overflow: hidden; }
  @media screen and (min-width: 330px) {
    .portfolio-item {
      width: 100%; } }
  @media screen and (min-width: 480px) {
    .portfolio-item {
      width: 80%;
      margin: 10px; } }
  @media screen and (min-width: 768px) {
    .portfolio-item {
      width: 45%; } }
  @media screen and (min-width: 1024px) {
    .portfolio-item {
      width: 30%; } }

.portfolio-item__wrapper {
  position: relative;
  padding-top: 62%; }

.portfolio-item__image {
  float: none !important;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  max-width: 100%; }

.portfolio-item__link-wrapper {
  height: auto;
  max-height: 100%;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 10px 5px 0 5px;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  background: rgba(0, 0, 0, 0.7);
  color: white; }

.portfolio-item__header {
  color: #b8bd56;
  font: bold 1.2rem/1.3rem Helvetica;
  text-align: center; }

.portfolio-item__short-description {
  color: #b8bd56;
  margin-bottom: 10px;
  text-align: center; }

.portfolio-item__description {
  overflow: hidden;
  height: 0;
  -webkit-transition: 1s height;
  -o-transition: 1s height;
  transition: 1s height; }
  .portfolio-item:hover .portfolio-item__description,
  .portfolio-item__link-wrapper:active .portfolio-item__description,
  .portfolio-item__link-wrapper:focus .portfolio-item__description {
    height: 200px; }

.portfolio-item__description_text {
  display: inline-block;
  vertical-align: top;
  width: 65%;
  text-align: right;
  font-size: 0.7rem;
  line-height: 0.9rem; }

.portfolio-item__technologies-list {
  float: left;
  display: inline-block;
  vertical-align: top;
  text-align: left;
  width: 35%;
  font-size: 0.8rem;
  text-transform: uppercase; }

.portfolio-item__technologies-item {
  display: block;
  color: #b8bd56; }

.portfolio-item__print-link {
  display: none; }



