.portfolio-filter__item {
  display: inline-block;
  margin: 10px 5px; }

.portfolio-filter__checker[type='checkbox'] {
  position: absolute;
  opacity: 0;
  left: -10000px;
  pointer-events: none; }
  .portfolio-filter__checker[type='checkbox'] + .portfolio-filter__marker {
    position: relative;
    padding: 5px 10px;
    background: rgba(184, 189, 86, 0.2);
    border-radius: 20px;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s; }
  .portfolio-filter__checker[type='checkbox']:checked + .portfolio-filter__marker {
    background: rgba(0, 204, 204, 0.8); }
  .portfolio-filter__checker[type='checkbox']:focus + .portfolio-filter__marker {
    -webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5); }

@media screen and (max-width: 640px) {
  .portfolio-filter__checker {
    margin: 5px; }
    .portfolio-filter__checker[type='checkbox'] + .portfolio-filter__marker {
      padding: 3px 6px;
      font-size: 1rem; } }



